@import "vars";


input,
input:focus,
input.focus-visible {
  border: none;
  outline: 0;
}

label {
  margin-bottom: .7rem;
}


.reserv {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: 3.3rem;

}

.hotel,
.date__enter,
.date__exit,
.quantity {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;

}

.hotel {
  position: relative;
  font-size: 1.2rem;
  margin-right: .5rem;

  &::before {
    content: '';
    display: block;
    width: 0.2rem;
    height: 3.2rem;
    position: absolute;
    top: .6rem;
    right: 0;
    background-color: $color-br-light;
  }

}

.hotel__choice {
  display: flex;
  flex-direction: column;


  span {
    margin-bottom: .7rem;
  }
}

.hotel__input {
  padding-left: 2rem;
  padding-right: .5rem;
  font-family: 'Fira Sans';
  font-size: 1.8rem;

}

.geo-svg {
  position: absolute;
  top: 2rem;
  left: 0;
  width: 1.5rem;
  height: 2rem;
  fill: none;
  stroke-width: .8;
  stroke: $color-accent;
  margin-right: .8rem;
}




.date {
  display: flex;

  position: relative;
  font-family: 'Proxima Nova Rg';
  font-size: 1.2rem;

  padding-right: 2.5rem;
  margin-right: 2.5rem;

  &::before {
    content: '';
    display: block;
    width: 0.2rem;
    height: 3.2rem;
    position: absolute;
    top: .6rem;
    right: 0;
    background-color: $color-br-light;
  }
}

.date__input {
  font-family: 'Fira Sans';
  font-weight: bold;
  font-size: 1.8rem;
  color: $color-text-second;
  cursor: pointer;
  max-width: 13.8rem;
}

.date__enter,
.date__exit {
  align-items: flex-start;
  position: relative;
  padding-left: 2.5rem;
}

.date__enter--label {
  margin-left: -2rem;
}

.date__enter::before {
  content: "";
  display: block;
  width: 2rem;
  height: 2rem;

  background: url("../img/calendar.svg") no-repeat;
  background-size: 2rem 2rem;

  position: absolute;
  top: 1.9rem;
  left: 0
}

.date__enter::after {
  content: "";
  display: block;
  width: .2rem;
  height: .2rem;
  background-color: $color-input;

  position: absolute;
  bottom: .5rem;
  right: 0;
}

.date__exit::after {
  content: "";
  display: block;
  width: 1rem;
  height: 1rem;


  border-top: .2rem solid;
  border-right: .2rem solid;
  border-color: $color-input;
  transform: rotate(135deg);
  cursor: pointer;
  z-index: 10;

  position: absolute;
  bottom: .5rem;
  right: 0;
}


.reserv .quantity {
  align-items: flex-start;
  font-family: 'Proxima Nova Rg';
  font-size: 1.2rem;
  margin-right: 2.5rem;

  position: relative;


  input {
    width: 3rem;
    height: 2.7rem;
    font-size: 1.6rem;
    text-align: center;
    color: $color-input;
    border: 1px solid $color-input;
  }

  .input-btn {
    position: absolute;
    width: 0;
    height: 0;

    left: 3.2rem;

    border: .5rem solid transparent;
    background-color: transparent;
    cursor: pointer;
    z-index: 10;
  }

  .input-btn__top {
    bottom: 1.8rem;

    border-top: 0;
    border-bottom-color: $color-accent;

  }

  .input-btn__down {
    bottom: .4rem;

    border-bottom: 0;
    border-top-color: $color-input;
  }



}


.circle-plus {
  width: 35px;
  height: 35px;
  border-radius: 50%;
  background-color: $color-accent;
  display: inline-flex;
  justify-content: center;
  align-items: center;


  &::before {
    content: '+';
    color: $color-white;
    display: block;
    font-size: 3rem;
    font-weight: bold;
    margin-left: -1px;
  }
}



.btn {
  display: flex;
  align-items: center;

  color: $color-white;
  padding: 2.3rem 5.5rem;

  background: $color-accent;
  border-radius: 4px;

  font-family: 'Proxima Nova Rg';
  font-weight: bold;
  letter-spacing: 2px;
  text-transform: uppercase;
}


// @media (min-width: 1040px) {}

@media (max-width: 1200px) {

  .container {
    width: 100%;
  }

  .reserv {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 2rem;

  }

  .hotel,
  .hotel__choice,
  .date__enter,
  .date__exit,
  .quantity {
    margin: 0;
    margin-bottom: 2rem;
    align-items: center;
    justify-content: center;
  }

  .hotel {
    &::before {
      display: none;
    }
  }

  .date {
    margin: 0;
    flex-direction: column;

    &::before {
      display: none;
    }
  }

  .reserv .quantity {
    margin-right: 0;
    width: 5rem;
  }

  .circle-plus {
    margin-bottom: 2rem;
  }

}
